<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :model="model"
        :entity-name="$t('人员管理')"
        :icon="'mdi-account'"
      >
        <template v-slot:filterRight>
          <v-btn
            class="ma-4"
            color="red darken-2"
            dark
            outlined
            @click="dialog = true"
          >
            {{ $t('打印跑堂日结单') }}
            <v-icon
              dark
              right
            >
              mdi-plus
            </v-icon>
          </v-btn>
          <v-dialog
            v-model="dialog"
            max-width="500px"
          >
            <v-card>
              <v-card-title>
                <span class="headline">{{ $t('打印跑堂日结单') }}</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      md="12"
                      sm="12"
                    >
                      <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :return-value.sync="dates"
                        offset-y
                      >
                        <template v-slot:activator="{ on }">
                          <v-text-field
                            v-model="dates"
                            hide-details
                            :label="$t('日期筛选')"
                            prepend-icon="mdi-calendar"
                            single-line
                            v-on="on"
                          />
                        </template>
                        <v-date-picker
                          v-model="dates"
                          full-width
                          no-title
                          range
                          scrollable
                          locale="de"
                        >
                          <v-spacer/>
                          <v-btn
                            text
                            color="primary"
                            @click="menu = false"
                          >
                            {{ $t('Cancel') }}
                          </v-btn>
                          <v-btn
                            text
                            color="primary"
                            @click="$refs.menu.save(dates)"
                          >
                            {{ $t('Ok') }}
                          </v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer/>
                <v-btn
                  color="blue darken-1"
                  text
                  @click="dialog = false"
                >
                  {{ $t('Cancel') }}
                </v-btn>
                <v-btn
                  color="blue darken-1"
                  @click="printAllServantBon"
                >
                  {{ $t('打印') }}
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            class="mr-2"
            small
            @click="goNext(item)"
          >
            mdi-arrow-right
          </v-icon>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>
  import GlobalSettings from '@/GlobalSettings'
  import IKUtils from 'innerken-js-utils'
  import { IkDataTable } from 'metaflow-js'
  import Servant from '../../model/Servant'
  import { printServantSummaryBon } from '../../components/Utlis/api'
  import { formatDate } from '../../common/common'

  export default {
    components: { IkDataTable },
    data: function () {
      return ({
        model: Servant,
        dates: [],
        menu: false,
        dialog: false,
      })
    },
    computed: {
      ImageRoot () {
        return this.$store.getters.rootUrl + GlobalSettings.servantRoot
      },
    },
    mounted () {
      this.dates = [formatDate(0), formatDate(0)]
    },
    methods: {
      clear () {
        this.dates = []
      },
      async printAllServantBon () {
        if (this.dates[1] === undefined) {
          this.dates[1] = this.dates[0]
        }
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        const servantArr = (await Servant.getList())
        servantArr.forEach(d => {
          printServantSummaryBon(d.id, this.dates[0], this.dates[1]).then(() => {
            IKUtils.toast()
            this.menu = false
            this.dialog = false
          })
        })
      },
      goNext (item) {
        this.$router.push({
          name: 'Keller Detail Seite',
          params: { id: item.id },
        })
      },
    },
  }
</script>
